import React from 'react';
import {Modal} from 'antd';
import MyScroll from '../../../components/my-scrollbar';
import { inject, observer } from 'mobx-react';   
import '../modal.css';
import './order_detail_popup.less';


@inject('orderStore') @observer
class OrderDetailPopup extends React.Component{
     

    //关闭按钮
    handleCancel=()=>{
      if(this.props.closebutton){
        this.props.closebutton();
      }
    }  

    render(){
       let orderStore=this.props.orderStore; 
		   let msgobj=orderStore.msgobj;  
       let subOrderlist=orderStore.subOrderlist; 
       let num = 1;   
        return(
            <Modal title="订单详情" 
                visible={true} 
                maskClosable={false} 
                onCancel={this.handleCancel} 
                footer={null} 
                width={900} wrapClassName="orderdetail-popup-modal" 
              >  
              <div id="detail-main">
                  <div className="detail-left">
                      <div className="detail-left-one">
                        <div className="each-datil">
                          <p> <span>订单号:</span> <em>{msgobj.orderCode}</em> </p>
                          <p> <span>桌台:</span>  <em>{msgobj.tableName}</em> </p> 
                        </div> 
                        <div className="each-datil">
                          <p> <span>开台时间:</span> <em>{msgobj.createTime}</em> </p>
                          <p> <span>开台人:</span>  <em>{msgobj.createName}</em> </p> 
                        </div>
                        <div className="each-datil">
                          <p> <span>人数:</span> <em>{msgobj.customerNumber}</em> </p>
                          <p> <span>服务员:</span>  <em>{msgobj.serverName}</em> </p> 
                        </div>
                        <div className="each-remark">
                          <span>整单备注:</span>
                          <em>{msgobj.memo}</em>
                        </div>
                        <div className="each-remark">
                          <span>关联桌台:</span>
                          <em>{msgobj.combineMemo}</em>
                        </div>

                      </div>
                      <div className="detail-left-two">
                         <div className="each-datil">
                            <p> <span>消费金额:</span> <em>{msgobj.actualAmount}</em> </p>
                            <p> <span>赠菜金额:</span>  <em>{msgobj.largess}</em> </p> 
                         </div>
                         <div className="each-datil">
                            <p> <span>折扣金额:</span> <em>{msgobj.discount}</em> </p>
                            <p> <span>服务费:</span>  <em>{msgobj.serverAmount}</em> </p> 
                         </div>
                          <div className="each-datil">
                            <p> <span>减免金额:</span> <em>{msgobj.derateAmount}</em> </p>
                            <p> <span>减免人:</span>  <em>{msgobj.deratePeople}</em> </p> 
                         </div>
                          <div className="each-datil">
                            <p> <span>抹零金额:</span> <em>{msgobj.noChange}</em> </p>
                            <p> <span>应收:</span>  <em>{msgobj.receivable}</em> </p> 
                         </div>

                      </div>
                      {msgobj.makeCollectionsRecode !== "" && 
                        <div className="detail-left-three">
                          <p>收款纪录</p>
                          <span>{msgobj.makeCollectionsRecode}</span>
                        </div>
                      }
                      
                  </div> 
                  <div className="detail-right">
                    <p>菜品详情</p>
                     <MyScroll width={418} height={437} >
                        <div className="detail-right-main"> 
                            {(() =>{ 
                                if(subOrderlist.length) {
                                    return subOrderlist.map((msgorder,index) => {    
                                      return <div className="detail-right-data" key={index}>
                                                <div className="many-table-info">
                                                  <div className="table-num">{msgorder.tableName}</div>
                                                  <div className="order-num">{msgorder.orderCode}</div>
                                                </div>
                                                {msgorder.foodlist.map((foodall, i)=>{ 
                                                  	  let tag;
                                                      
                                                      switch(foodall.aLaCarteMethod) {
                                                         case 684: //正常点菜
                                                          tag = 
                                                           <div className="order-list" key={i}>
                                                                <span className="num">{num}</span>
                                                                <span className="name">{foodall.productName}</span>
                                                                <span className="count">{foodall.quantity}</span>
                                                                <span className="price">￥{foodall.price}</span> 
                                                                {foodall.optionlist.map((optionl, i)=>{
                                                                  return  <div className="remark" key={i}>{foodall.optionName}         ￥{foodall.addedPrice}</div>
                                                                })}
                                                            </div>
                                                            num++;
                                                          break;
                                                        case 685: //退菜
                                                          tag = <div className="order-list return-list" key={i}>
                                                                  <span className="return">退</span>
                                                                  <span className="name">{foodall.memo}</span>
                                                                  <span className="count">{foodall.quantity}</span>
                                                                  <span className="price">￥{foodall.price}</span> 
                                                                </div> ;
                                                          break;
                                                        case 686: //赠菜
                                                          tag = <div className="order-list return-list" key={i}>
                                                                  <span className="return">赠</span>
                                                                  <span className="name">{foodall.memo}</span>
                                                                  <span className="count">{foodall.quantity}</span>
                                                                  <span className="price">￥{foodall.price}</span> 
                                                                </div>;
                                                          break;
                                                        case 687: //加菜
                                                          tag =  <div className="order-list return-list" key={i}>
                                                                  <span className="return">加</span>
                                                                  <span className="name">{foodall.memo}</span>
                                                                  <span className="count">{foodall.quantity}</span>
                                                                  <span className="price">￥{foodall.price}</span> 
                                                                </div>;
                                                          break;
                                                        case 996: //转菜
                                                          tag =  <div className="order-list return-list" key={i}>
                                                                  <span className="return">转</span>
                                                                  <span className="name">{foodall.memo}</span>
                                                                  <span className="count">{foodall.quantity}</span>
                                                                  <span className="price">￥{foodall.price}</span> 
                                                                </div>;
                                                          break;
                                                        case 997: //换菜
                                                          tag =  <div className="order-list return-list" key={i}>
                                                                  <span className="return">换</span>
                                                                  <span className="name">{foodall.memo}</span>
                                                                  <span className="count">{foodall.quantity}</span>
                                                                  <span className="price">￥{foodall.price}</span> 
                                                                </div>;
                                                          break;
                                                        case 998: //转赠
                                                          tag =  <div className="order-list return-list" key={i}>
                                                                  <span className="return">转</span>
                                                                  <span className="name">{foodall.memo}</span>
                                                                  <span className="count">{foodall.quantity}</span>
                                                                  <span className="price">￥{foodall.price}</span> 
                                                                </div>;
                                                          break;
                                                        default:
                                                      }
                                                      return tag;
                                                  
                                                })}
                                              </div>
                                    });
                                }
                            })()}  
                        </div>
                        
                   </MyScroll>
                  </div>
              </div>
        </Modal>
      )
    }
}

export default OrderDetailPopup;


